<template>
    <v-main>
        <v-container v-if="!$route.params.user_id">
            <v-breadcrumbs :items="history">
                <template v-slot:divider>
                    <v-icon>mdi-chevron-right</v-icon>
                </template>
            </v-breadcrumbs>

            <UserTable :subtitle="'一般用戶'" />
            <CompanyTable :subtitle="'原料商'" />
            <AgentTable :subtitle="'管理員'" />
            <AdminTable :subtitle="'專員'" />

            <v-btn
                bottom
                color="pink"
                dark
                fab
                fixed
                right
                @click="bus.$emit('dialogAddUser');"
                v-if="PermissionName !== 'user'
                    && PermissionName !== 'guest'"
            >
                <v-icon>mdi-plus</v-icon>
            </v-btn>
        </v-container>

        <v-container v-else>
            <v-breadcrumbs :items="history">
                <template v-slot:divider>
                    <v-icon>mdi-chevron-right</v-icon>
                </template>
            </v-breadcrumbs>

            <UserDetail />
        </v-container>

        <template v-slot:prepend>
            <v-list-item twoLine>
                <v-list-item-avatar>
                    <img src="https://randomuser.me/api/portraits/women/81.jpg" />
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title>Jane Smith</v-list-item-title>
                    <v-list-item-subtitle>Logged In</v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
        </template>

        <DialogAddMaterial />
    </v-main>
</template>

<script>
import bus from '@/bus';
import { mapState, mapGetters, mapActions } from 'vuex';
import DialogAddMaterial from '../Dialog/AddMaterial';
import UserDetail from './details/UserDetail';
import UserTable from './tables/UserTable';
import CompanyTable from './tables/CompanyTable';
import AgentTable from './tables/AgentTable';
import AdminTable from './tables/AdminTable';

export default {
    components: {
        DialogAddMaterial, UserDetail, UserTable, CompanyTable, AgentTable, AdminTable
    },
    data: () => ({
        bus
    }),
    computed: {
        history() {
            const history = [
                {
                    text: '用戶管理'
                }
            ];
            if (this.$route.params.user_id) {
                history[0].href = '/#/user';
                history.push({
                    text: '用戶資料'
                });
            }
            return history;
        },
        ...mapGetters({
            PermissionName: 'getPermissionName'
        }),
        ...mapState(['Materials', 'Users'])
    },
    mounted() {
        if (this.Users.length === 0) this.$store.dispatch('actionFetchUsers');
    },
    methods: {
        ...mapActions(['actionFetchUsers'])
    }
};
</script>
